import React,{useState} from 'react'
import {db} from '../../common/Model'
import { useLiveQuery } from 'dexie-react-hooks';

import './CalendarForm.less'

const reg = /^\d*?$/;

const CalendarForm = (props) => {
  const [mission,setMission] = useState('')
  const [time,setTime] = useState('')

  const saveMission = (e) => {
    setMission(e.target.value)
  }
  const saveTime = (e) => {
    if(!reg.test(e.target.value)){
      alert('è«‹è¼¸å…¥æ•¸å­—')
      return 
    }
    setTime(e.target.value)
  }
  const Submit = () => {
    if(!mission || !time){
      alert('ä»»å‹™åç¨±æˆ–æ™‚é–“ä¸èƒ½ç‚ºç©º!')
      return
    }
    if(mission.length > 9){
      alert('ä»»å‹™åç¨±ä¸èƒ½è¶…é9å€‹å­—!')
      return
    }
    if(time > 9999){
      alert('æ™‚é–“ä¸èƒ½è¶…é9999åˆ†é˜!')
      return
    }
    const now = new Date()
    db.plan.add({ 
      id:now.getTime(),
      mission:mission, 
      time:time, 
      smoke:false,
      year:props.timeInfo.year,
      month:props.timeInfo.month,
      date:props.timeInfo.date,
      state:'wait',
    });
    alert('æäº¤æˆåŠŸ')
    setMission('')
    setTime('')
    props.closeForm()
  }

  const deletePlan = (id) => {
    db.plan.delete(id)
  }

  const planLi = useLiveQuery(
    ()=>db.plan.where({
        year:props.timeInfo.year,
        month:props.timeInfo.month,
        date: props.timeInfo.date,
      }).toArray()
    ,[props.timeInfo]
  )

  const addPlan = () =>{
    const {initYear,initMonth,today,year,month,date } = props.timeInfo
    const now = new Date(initYear, initMonth-1, today);
    const select = new Date(year, month-1, date);
    const nowTime = now.getTime()
    const selectTime = select.getTime()

    if(nowTime <= selectTime){
      return(
        <div className='CalendarForm__inner__addPlan'>
          <strong> æ–°å¢è¨ˆåŠƒï¼š</strong>
          <div>åç¨±  <input type="text" name="mission" autoComplete='off' onChange={saveMission.bind(this)} value={mission}/></div>
          <div>æ™‚é–“  <input type="text" name="time" autoComplete='off' onChange={saveTime} value={time} placeholder='å–®ä½:åˆ†é˜'/></div>
          <div>
            <input type="button" value="å–æ¶ˆ" onClick={props.closeForm} />
            <input type="button" value="æäº¤" onClick={Submit}/>
          </div>
        </div>
      )
    }
    return null
  }

  return(
    <div className='CalendarForm'  > 
      <div className='CalendarForm__outside' onClick={props.closeForm}></div>
      <form className='CalendarForm__inner'>
        <div className='CalendarForm__inner__close' onClick={props.closeForm}></div>
        <div className='CalendarForm__inner__date'>{props.timeInfo.date}</div>
        <div className='CalendarForm__inner__plan'>
          <strong> è¨ˆåŠƒï¼š</strong>
          <ul>
            {planLi 
              ? planLi.map((e)=> <li key={e.id}>{e.mission}/{e.time}åˆ†é˜<div onClick={()=>{deletePlan(e.id)}}>åˆªé™¤</div></li>) 
              : ''
            }
          </ul>
        </div>
        {addPlan()}
      </form>
    </div>  
  )
}
export default CalendarForm